<template>
<div id="rentCar">
   <h2>商家如何新建门店？</h2>
  <p>微信搜索“e换电租车商家后台”登录，若无账号需联系e换电工作人员开通。登录后进入工作台页面，如图18所示,点击其他-门店管理，新增门店，一个商户可创建多个门店。门店支持编辑、删除操作。门店支持长租和短租，用户根据门店地址进行取车、还车。</p>
  <div class="imgDiv">
    <img src="../../assets/m1.jpg" alt="">
  </div>
  <div class="imgDiv">
    <img src="../../assets/m2.jpg" alt="">
  </div>
</div>
</template>

<script>
    export default {
        name: "rentCar"
    }
</script>

<style scoped lang="scss">
#rentCar{
 padding: 0 40px;
  box-sizing: border-box;
  h2{
    font-size:28px;
    font-family:PingFangSC-Medium;
    font-weight:500;
    color:rgba(51,51,51,1);
    line-height: 100px;
    border-bottom: 1px solid #F0F0F0;
    text-align: left;
  }
  p{
    font-size:28px;
    font-family:PingFangSC-Regular;
    font-weight:400;
    color:rgba(72,72,72,1);
    line-height:40px;
    margin-top: 38px;
    text-align: left;
  }
  .imgDiv{
    width:400px;
    margin: 40px auto;
    margin-top: 70px;
    img{
      height: 100%;
      width: 100%;
    }
  }
}
</style>



